"use client"

import { useState, useEffect, useCallback } from "react"
import supabase from "@/utils/supabaseClient"

type ScheduledPost = {
  id: string
  content: string
  scheduled_at: string
  platform: string
  status: string
}

type CalendarEvent = {
  id: string
  title: string
  start: Date
  end: Date
  platform: string
  status: string
}

export default function SchedulerPage() {
  const [events, setEvents] = useState<CalendarEvent[]>([]) // Scheduled posts as calendar events
  const [loading, setLoading] = useState(true)

  const fetchScheduledPosts = useCallback(async () => {
    setLoading(true)
    const { data, error } = await supabase
      .from<ScheduledPost>("scheduled_posts")
      .select("*")
      .order("scheduled_at", { ascending: true })
    if (!error && data) {
      const mappedEvents: CalendarEvent[] = data.map((post) => ({
        id: post.id,
        title: post.content.substring(0, 20) + (post.content.length > 20 ? "..." : ""),
        start: new Date(post.scheduled_at),
        end: new Date(post.scheduled_at),
        platform: post.platform,
        status: post.status,
      }))
      setEvents(mappedEvents)
    }
    setLoading(false)
  }, [])

  const eventStyleGetter = (event: CalendarEvent) => {
    let backgroundColor = "#3174ad" // default blue
    if (event.status === "posted") backgroundColor = "#4caf50" // green
    else if (event.status === "failed") backgroundColor = "#f44336" // red
    else if (event.status === "scheduled") backgroundColor = "#ff9800" // orange

    return {
      style: {
        backgroundColor,
        borderRadius: "4px",
        color: "white",
        border: "none",
        padding: "2px 4px",
      },
    }
  }

  const onSelectEvent = (event: CalendarEvent) => {
    alert(`Post: ${event.title}\nPlatform: ${event.platform}\nStatus: ${event.status}`)
  }

  return (
    <div className="p-6">
      <h1 className="text-3xl font-bold mb-4">Content Scheduler</h1>
      {loading ? (
        <p>Loading scheduled posts...</p>
      ) : (
        <div>
          {/* Calendar UI will be implemented here */}
          <p>Calendar UI placeholder (react-big-calendar not installed)</p>
        </div>
      )}
    </div>
  )
}
